<template>
  <div class="navigation" v-if="false">
    <div class="baba" v-for="(slide, index) in carouselSlide" :key="index">
      <div v-show="currentSlide === index + 1" class="slide-info">
        <i @click="prevSlide" class="fa-solid fa-angle-left"></i>
        <img :src="require(`../assets/images/${slide}.jpg`)" alt="" />

        <i @click="nextSlide" class="fa-solid fa-angle-right"></i>
      </div>
    </div>
    <div class="pagination">
      <span
        @click="goToSlide(index)"
        v-for="(slide, index) in getSlideCount"
        :key="index"
        :class="{ active: index + 1 === currentSlide }"
      ></span>
    </div>
  </div>

  <div class="baba">
    <div class="contact">
      <div class="icon">
        <div class="det">
          <i class="fa-brands fa-linkedin"></i>
          <div class="col">
            <h2>Our Location</h2>
            <h3>123 Street, New York, USA</h3>
          </div>
        </div>
        <div class="det">
          <i class="fa-brands fa-linkedin"></i>
          <div class="col">
            <h2>Call Us</h2>
            <h3>+012 345 6789</h3>
          </div>
        </div>
        <div class="det">
          <i class="fa-brands fa-linkedin"></i>
          <div class="col">
            <h2>Email Us</h2>
            <h3>info@example.com</h3>
          </div>
        </div>
      </div>
    </div>
    <div class="help">
      <h5>Need Help</h5>
      <h1>Send Us Message</h1>
      <form action="" @submit.prevent="submitForm">
        <input type="text" placeholder="Your Name" v-model="forms.FullName" />
        <input
          type="email"
          placeholder="Your Email"
          v-model="forms.EmailAdress"
        />
        <input type="text" placeholder="subject " v-model="forms.subject" />
        <div>
          <textarea
            name="massage"
            id=""
            cols="52"
            rows="10"
            placeholder="Massage"
            v-model="forms.massage"
          ></textarea>
        </div>
        <button class="btn">submit</button>
      </form>
    </div>
  </div>
</template>

<script>
import { ref, onMounted } from "vue";

export default {
  data() {
    return {
      forms: {
        FullName: "",
        EmailAdress: "",
        subject: "",
        massage: "",
      },
    };
  },
  methods: {
    submitForm() {
      alert("submit");
    },
  },
  setup() {
    const currentSlide = ref(1);
    const getSlideCount = ref(null);
    const autoPlayEnable = ref(true);
    const timeoutDuration = ref(10000);
    const carouselSlide = [
      "img-1",
      "img-2",
      "img-3",
      "img-4",
      "img-5",
      "img-6",
      "img-7",
      "img-8",
      "img-9",
    ];

    // next slide
    const nextSlide = () => {
      if (currentSlide.value === getSlideCount.value) {
        currentSlide.value = 1;
        return;
      }
      currentSlide.value += 1;
    };
    // prev slide
    const prevSlide = () => {
      if (currentSlide.value === 1) {
        currentSlide.value = 1;
        return;
      }
      currentSlide.value -= 1;
    };
    // span
    const goToSlide = (index) => {
      currentSlide.value = index + 1;
    };

    // autoplay
    const autoPlay = () => {
      setInterval(() => {
        nextSlide();
      }, timeoutDuration.value);
    };

    if (autoPlayEnable.value) {
      autoPlay();
    }
    onMounted(() => {
      getSlideCount.value = document.querySelectorAll(".baba").length;
      //  عددهم
      console.log(getSlideCount.value);
      // getSlideCount.value = document.querySelectorAll(".carosel").length;
      // console.log(getSlideCount.value);
    });

    return {
      carouselSlide,
      currentSlide,
      getSlideCount,
      nextSlide,
      prevSlide,
      goToSlide,
    };
  },

  mounted() {},
  components: {},
};
</script>

