<script>
import ChooseUs from "./ChooseUs.vue";

export default {
  data() {
    return {
      count1: 0,
      count2: 0,
      count3: 0,
      count4: 0,
      scrollStart: null,
    };
  },
  methods: {
    increament() {
      setInterval(() => {
        this.scrollStart = window.scrollY;
        if (this.scrollStart >= 510 && this.count1 <= 124) {
          this.count1++;
        }
      }, 0.2);
      setInterval(() => {
        if (this.scrollStart >= 510 && this.count2 <= 1300) {
          this.count2++;
        }
      }, 0.2);
      setInterval(() => {
        if (this.scrollStart >= 510 && this.count3 <= 200) {
          this.count3++;
        }
      }, 0.2);
      setInterval(() => {
        if (this.scrollStart >= 510 && this.count4 <= 1000) {
          this.count4++;
        }
      }, 0.2);
    },
  },
  created() {
    this.increament();
  },
  components: { ChooseUs },
};
</script>

