export default {
  data() {
    return {
      menus: [
        { id: "1", link: "Home", url: "/" },
        { id: "2", link: "About", url: "/about", section: "#about" },
        { id: "3", link: "Courses", url: "/courses" },
        { id: "4", link: "Contact", url: "/contact" },
      ],
      scrolledNav: null,
      mobile: null,
      mobileNav: null,
      windowWidth: null,
    };
  },
  methods: {
    toggleMobileNav() {
      this.mobileNav = !this.mobileNav;
    },
    cheakScreen() {
      this.windowWidth = innerWidth;
      if (this.windowWidth <= 1024) {
        this.mobile = true;
        return;
      }
      this.mobile = false;
      this.mobileNav = false;
    },
  },
  created() {
    window.addEventListener("resize", this.cheakScreen);
    this.cheakScreen();
  },
  components: { TopNav },
};
</script>

