<template>
  <slot>
    <transition name="fade">
      <img
        v-show="show"
        @click="scrollTop"
        class="top-page"
        src="https://img.icons8.com/fluency/48/null/circled-up-2.png"
      />
    </transition>
  </slot>
</template>

<script>
export default {
  data() {
    return {
      show: null,
    };
  },
  methods: {
    scrollTop() {
      window.scrollTo({ top: 0, behavior: "smooth" });
    },
    onscroll() {
      this.show = !this.show;
      this.show = window.scrollY;
      if (this.show >= 50) {
        this.show = true;
        return;
      }
      this.show = false;
    },
  },
  created() {
    window.addEventListener("scroll", this.onscroll);
    this.onscroll();
  },
};
</script>

<style>
.top-page {
  border: none;
  font-weight: bold;
  border-radius: 4px;
  position: fixed;
  bottom: 20px;
  right: 8px;
  cursor: pointer;
  width: 40px;
  z-index: 1000;
}
.fade-enter-active,
.fade-leave-active {
  transition: 1s ease-in;
}
.fade-enter-from,
.fade-leave-to {
  transform: translateY(100px);
  opacity: 0.2;
}
</style>
